.list ul {
  background: #252525;
  padding-top: 5px;
}

.list ul input:focus {
  border-bottom: 2px solid #FDAB18;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.list ul input {
  border-bottom: 2px solid transparent;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

